@tailwind base;
@tailwind components;
@tailwind utilities;

/* Ant Design overrides and custom styles */
.ant-layout {
  min-height: 100vh;
}

.ant-layout-sider {
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.06);
}

.ant-layout-header {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
}

.ant-menu-dark .ant-menu-item-selected {
  background-color: #1890ff !important;
}

.ant-card {
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
}

.ant-table-thead > tr > th {
  background-color: #fafafa;
  font-weight: 600;
}

.ant-btn-primary {
  background: linear-gradient(135deg, #1890ff 0%, #40a9ff 100%);
  border: none;
  box-shadow: 0 2px 4px rgba(24, 144, 255, 0.2);
}

.ant-btn-primary:hover {
  background: linear-gradient(135deg, #40a9ff 0%, #1890ff 100%);
  box-shadow: 0 4px 8px rgba(24, 144, 255, 0.3);
}

.ant-input:focus,
.ant-input-focused {
  border-color: #1890ff;
  box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
}

.ant-select-focused .ant-select-selector {
  border-color: #1890ff !important;
  box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2) !important;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* Loading animation */
.ant-spin-dot {
  font-size: 24px;
}

/* Chart container */
.recharts-wrapper {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .ant-layout-sider {
    position: fixed !important;
    height: 100vh;
    z-index: 1000;
  }
  
  .ant-layout-content {
    margin-left: 0 !important;
  }
}

/* Custom admin loading animation */
.admin-loading {
  @apply inline-block w-8 h-8 border-4 border-admin-200 border-t-admin-600 rounded-full animate-spin;
}

.admin-loading-small {
  @apply inline-block w-4 h-4 border-2 border-admin-200 border-t-admin-600 rounded-full animate-spin;
}

/* Custom admin button styles */
.btn-admin {
  @apply px-4 py-2 bg-admin-600 text-white rounded-md hover:bg-admin-700 focus:outline-none focus:ring-2 focus:ring-admin-500 focus:ring-offset-2 transition-colors duration-200;
}

.btn-admin-secondary {
  @apply px-4 py-2 bg-admin-100 text-admin-700 rounded-md hover:bg-admin-200 focus:outline-none focus:ring-2 focus:ring-admin-500 focus:ring-offset-2 transition-colors duration-200;
}

/* Custom admin card styles */
.card-admin {
  @apply bg-white rounded-lg shadow-admin border border-admin-200;
}

.card-admin-header {
  @apply px-6 py-4 border-b border-admin-200;
}

.card-admin-body {
  @apply px-6 py-4;
}

.card-admin-footer {
  @apply px-6 py-4 border-t border-admin-200 bg-admin-50;
}

/* Custom admin form styles */
.form-input-admin {
  @apply block w-full px-3 py-2 border border-admin-300 rounded-md shadow-sm placeholder-admin-500 focus:outline-none focus:ring-admin-500 focus:border-admin-500;
}

.form-label-admin {
  @apply block text-sm font-medium text-admin-700 mb-1;
}

/* Custom admin table styles */
.table-admin {
  @apply min-w-full divide-y divide-admin-200;
}

.table-admin-header {
  @apply bg-admin-50;
}

.table-admin-header th {
  @apply px-6 py-3 text-left text-xs font-medium text-admin-500 uppercase tracking-wider;
}

.table-admin-body tr {
  @apply hover:bg-admin-50 transition-colors duration-150;
}

.table-admin-body td {
  @apply px-6 py-4 whitespace-nowrap text-sm text-admin-900;
}

/* Custom admin badge styles */
.badge-admin {
  @apply inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium;
}

.badge-admin-success {
  @apply bg-success-100 text-success-800;
}

.badge-admin-warning {
  @apply bg-warning-100 text-warning-800;
}

.badge-admin-danger {
  @apply bg-danger-100 text-danger-800;
}

.badge-admin-info {
  @apply bg-admin-100 text-admin-800;
}

/* Custom admin navigation styles */
.nav-admin {
  @apply bg-admin-800 text-white;
}

.nav-admin-item {
  @apply px-3 py-2 rounded-md text-sm font-medium transition-colors duration-200;
}

.nav-admin-item:hover {
  @apply bg-admin-700 text-white;
}

.nav-admin-item.active {
  @apply bg-admin-600 text-white;
}

/* Custom admin sidebar styles */
.sidebar-admin {
  @apply bg-admin-900 text-white w-64 min-h-screen;
}

.sidebar-admin-header {
  @apply px-6 py-4 border-b border-admin-700;
}

.sidebar-admin-nav {
  @apply px-3 py-2;
}

.sidebar-admin-nav-item {
  @apply flex items-center px-3 py-2 text-sm font-medium rounded-md transition-colors duration-200;
}

.sidebar-admin-nav-item:hover {
  @apply bg-admin-700 text-white;
}

.sidebar-admin-nav-item.active {
  @apply bg-admin-600 text-white;
}

/* Custom admin content area styles */
.content-admin {
  @apply bg-admin-50 min-h-screen;
}

.content-admin-header {
  @apply bg-white shadow-sm border-b border-admin-200 px-6 py-4;
}

.content-admin-body {
  @apply p-6;
}

/* Responsive utilities */
@media (max-width: 640px) {
  .sidebar-admin {
    @apply w-full;
  }
  
  .content-admin-body {
    @apply p-4;
  }
}

/* Animation for success toast */
@keyframes slide-up {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.animate-slide-up {
  animation: slide-up 0.3s ease-out;
}

/* Smooth transitions for settings fields */
.admin-input {
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.admin-input:focus {
  outline: none;
  border-color: rgb(99 102 241);
  box-shadow: 0 0 0 3px rgb(99 102 241 / 0.1);
}

/* Custom scrollbar for settings panel */
.settings-scrollbar::-webkit-scrollbar {
  width: 6px;
}

.settings-scrollbar::-webkit-scrollbar-track {
  background: #f3f4f6;
  border-radius: 3px;
}

.settings-scrollbar::-webkit-scrollbar-thumb {
  background: #9ca3af;
  border-radius: 3px;
}

.settings-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #6b7280;
}

/* Settings field hover effects */
.settings-field {
  transition: all 0.2s ease;
}

.settings-field:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1);
}

/* Toggle switch animation */
.toggle-switch {
  transition: background-color 0.2s ease;
}

.toggle-switch-handle {
  transition: transform 0.2s ease;
}

/* Category button active state */
.category-active {
  position: relative;
}

.category-active::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 3px;
  height: 70%;
  background: currentColor;
  border-radius: 0 2px 2px 0;
}












